import PropTypes from 'prop-types';
import React, {PureComponent} from 'react';

import {imageURLForUser, isMobile, isGuest} from 'utils/utils.jsx';

import OverlayTrigger from 'components/overlay_trigger';
import ProfilePopover from 'components/profile_popover';
import BotBadge from 'components/widgets/badges/bot_badge';
import GuestBadge from 'components/widgets/badges/guest_badge';

const colors = [
    '#7cb305',
    '#faad14',
    '#9013FE',
    '#1890ff',
    '#389e0d',
    '#08979c',
    '#722ed1',
    '#eb2f96',
    '#fa8c16',
    '#fa541c',
    '#f5222d',
];
export default class UserProfile extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            bgColor: [
                '#1B5E20',
                '#7cb305',
                '#311B92',
                '#673AB7',
                '#01579B',
                '#1890ff',
                '#006064',
                '#009688',
                '#880E4F',
                '#eb2f96',
                '#fa8c16',
                '#f5222d',
                '#BF360C',
            ],
            selectedColor: '',
        };
    }
    static propTypes = {
        disablePopover: PropTypes.bool,
        displayName: PropTypes.string,
        hasMention: PropTypes.bool,
        hideStatus: PropTypes.bool,
        isBusy: PropTypes.bool,
        isRHS: PropTypes.bool,
        overwriteName: PropTypes.node,
        user: PropTypes.object,
        userId: PropTypes.string,
    };

    static defaultProps = {
        disablePopover: false,
        hasMention: false,
        hideStatus: false,
        isRHS: false,
        overwriteImage: '',
        overwriteName: '',
    };
    hideProfilePopover = () => {
        if (this.overlay) {
            this.overlay.hide();
        }
    };

    setOverlaynRef = (ref) => {
        this.overlay = ref;
    };

    componentDidMount = () => {
        var item = this.state.bgColor[Math.floor(Math.random() * this.state.bgColor.length)];
        this.setState({
            selectedColor: item,
        });
    };

    _getRandomColor = () => {
        var item = this.state.bgColor[Math.floor(Math.random() * this.state.bgColor.length)];
        this.setState({
            selectedColor: item,
        });
    };

    render() {
        const {
            disablePopover,
            displayName,
            isBusy,
            isRHS,
            hasMention,
            hideStatus,
            overwriteName,
            user,
            userId,
        } = this.props;

        const name = overwriteName || displayName || '...';
        if (disablePopover) {
            return <div className='user-popover'>{name}</div>;
        }

        let placement = 'right';
        if (isRHS && !isMobile()) {
            placement = 'left';
        }

        let profileImg = '';
        if (user) {
            profileImg = imageURLForUser(user);
        }
        return (
            <React.Fragment>
                <OverlayTrigger
                    ref={this.setOverlaynRef}
                    trigger='click'
                    placement={placement}
                    rootClose={true}
                    overlay={
                        <ProfilePopover
                            className='user-profile-popover'
                            userId={userId}
                            src={profileImg}
                            isBusy={isBusy}
                            hide={this.hideProfilePopover}
                            hideStatus={hideStatus}
                            isRHS={isRHS}
                            hasMention={hasMention}
                        />
                    }
                >
                    <button
                        aria-label={name.toLowerCase()}
                        className='user-popover style--none'
                        style={{color: this.state.selectedColor}}
                    >
                        {name}
                    </button>
                </OverlayTrigger>
                <BotBadge
                    show={Boolean(user && user.is_bot)}
                    className='badge-popoverlist'
                />
                <GuestBadge
                    show={Boolean(user && isGuest(user))}
                    className='badge-popoverlist'
                />
            </React.Fragment>
        );
    }
}
