import React from 'react';
import {FormattedMessage} from 'react-intl';

export default class ReplyIcon extends React.PureComponent {
    render() {
        return (
            <span {...this.props}>
                <FormattedMessage
                    id='generic_icons.reply'
                    defaultMessage='Reply Icon'
                >
                    {(title) => (
                        <i
                            className='fa fa-reply'
                            title={title}
                        />
                    )}
                </FormattedMessage>
            </span>
        );
    }
}

