import React from 'react';
import {FormattedMessage} from 'react-intl';

export default class DraftIcon extends React.PureComponent {
    render() {
        return (
            <span
                {...this.props}
                id='draftIcon'
            >
                <FormattedMessage
                    id='generic_icons.channel.draft'
                    defaultMessage='Channel Draft Icon'
                >
                    {(title) => (
                        <i
                            className='fas fa-pen'
                            title={title}
                        />
                    )}
                </FormattedMessage>
            </span>
        );
    }
}
